










































































.home {
  background-color: #f6f6f6;
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
  .appHomeHead {
    width: 100%;
    height: 8vh;
    line-height: 8vh;
    text-align: center;
    color: #fff;
    font-size: 4.44vw;
    font-family: MicrosoftYaHei, MicrosoftYaHei-Regular;
    font-weight: 400;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    position: relative;
    .back {
      width: 6vw;
      position: absolute;
      top: 4vw;
      left: 3vw;
    }
    .HeadText{
      font-size: 6.5vw;
    }
    img {
      width: 5vw;
      position: absolute;
      top: 4.5vw;
      right: 3vw;
    }
  }
  .homeGo {
    width: 100vw;
    height: 5vh;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
    a {
      height: 4vh;
      text-decoration: none;
      color: #006d5b;
      box-sizing: border-box;
      padding: 0 3vw;
    }
    .router-link-active {
      color: #006d5b;
      padding: 0 3vw;
      border-bottom: 0.5vw solid #006d5b;
    }
  }
  .ItemTop {
    width: 96vw;
    height: 17vh;
    padding: 3vw 0;
    margin: 0 auto;
    img {
      width: 100%;
      height: 30vw;
    }
    .mint-swipe {
      width: 100%;
      height: 30vw;
    }
  }
}
